@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&family=Quicksand:wght@400;700&display=swap');

body {
  font-family: 'Quicksand', sans-serif !important;
}



.f700 {
  font-weight: 700
}

.layout {
  width: 100%;
  height: 768px;

  display: grid;
  grid:
    "header header header" auto
    "leftSide body rightSide" 1fr
    / 10% 60% 10%;
  gap: 2% 5%;

  justify-content: space-around;
  align-items: center;
}

.header { grid-area: header; }
.leftSide { grid-area: leftSide; }
.body { grid-area: body; }
.rightSide { grid-area: rightSide; }


#birthday-form legend {
  display:none;
}

.logo-text {
  margin-left: 3%;
}
#navbarSupportedContent .nav-link {
width: 6rem;
line-height: 110%;
text-align: center;
vertical-align: middle;
}